<template lang="html">
  <div class="_full_router chargeBody">
    <custHeader :title="title1"></custHeader>

    <div class="card" @click="choseCard">
      <span style="float: right;margin-top: 12px;margin-right: 20px;">{{ bankTitle }}</span>
      <div style="padding-top: 66px;text-align: right; padding-right: 22px;">{{ bankAcct | bankcard}}</div>
    </div>
    <mt-field label="转账金额" placeholder="请输入转账金额" v-model="bizAmt"  type="number"></mt-field>
    <mt-button  type="primary" size="large"  @click="charge" style="margin-top: 10px;">充值</mt-button>


    <mt-popup
      v-model="popupVis"
      position="right"
      style="width:100%"
      >
      <div class="choseCardBody">
        <mt-header :title="title2" class="cust-header" style="background:#ef4f4f" ><mt-button icon="back" @click="back" slot="left">返回</mt-button></mt-header>
        <ul style="margin-top:10px;">
          <li class="cards" v-for="(item,index) in bankCards" @click="chose(item.bankTitle,item.bankAcct,item.bankCode)">
            <div style="text-align: right;margin-top: 12px;margin-right: 20px;height:20px;margin-bottom:10px;">{{ item.bankTitle }}</div>
            <div style="text-align: right; padding-right: 22px;height:50px;">{{ item.bankAcct | bankcard }}</div>
          </li>
          <li class="cards"><a href="#/personal/bankCardsAdd">+添加银行卡</a></li>
        </ul>
      </div>

    </mt-popup>
  </div>
</template>

<script>
import custHeader from  'components/custHeader'
import {MessageBox} from 'mint-ui'
export default {
  data(){
    return {
      title1:'充值',
      title2:'选择银行卡',
      bankTitle:'',
      bankCode:'',
      bankAcct:'',
      bankCards:[],
      bizAmt:'',
      popupVis:false
    }
  },
  created(){
    // var recBankBranchName = '中国银行'
    // var recBankAcct = '42202151555512354'
    // var recBankAcctName = '同花顺'
    // var bizAmt = '1000.00'
    //
    // MessageBox.alert("<div style='text-align:left'>尊敬的客户:</div><p style='text-indent:2em;text-align: left;'>您申请的业务已受理,请通过网银向下面的地址转账</p><div style='text-align: left;'><span style='width: 25%;display: inline-block;'>转入银行:</span>"
    //               +recBankBranchName+"</div><div style='text-align: left;'><span style='width: 25%;display: inline-block;'>转入户名:</span>"
    //               +recBankAcctName+"</div><div  style='text-align: left;'><span  style='width: 25%;display: inline-block;'>转入账号:</span>"
    //               +recBankAcct+"</div><div  style='text-align: left;'><span  style='width: 25%;display: inline-block;'>转入金额:</span>"
    //               +bizAmt+"元</div><div style='text-indent:2em;text-align: left;'>我们将在24小时内确认到账</div><div style='text-align:right'>宝三成</div><div style='text-align:right'>"+date+"</div>").then(action => {
    //   router.push("/personal")
    // })
    var self = this;
    axiosUtil.upCltFundAppQry().then(function(data){

        var data = data.data.data
        for (var i = 0; i < data.length; i++) {
          self.bankCards.push({bankCode:data[i].bankCode,bankAcct:data[i].bankAcct,bankTitle:data[i].orgName})
          // self.bankCards.push({bankCode:data[i].bankCode,bankAcct:data[i].bankAcct})
        }

        self.bankTitle = self.bankCards[0].bankTitle
        self.bankCode = self.bankCards[0].bankCode
        self.bankAcct = self.bankCards[0].bankAcct
    });



  },
  methods:{
    charge:function(){
      var self = this
      if(!self.bankCode){
        MessageBox.alert("请先去绑定银行卡").then(action => {
          router.push("/personal/bankCardsAdd")
        });
        return false
      }

      axiosUtil.upCltFundInApp({recBankCode:self.bankCode,recBankAcct:self.bankAcct,askBizAmt:self.bizAmt}).then(function(data){
        var data = data.data;
        if(data.errorcode){
          MessageBox("提示", data.errormsg);
        }else{
          var date = new Date().Format("yyyy年MM月dd日")
          MessageBox.alert("<div style='text-align:left'>尊敬的客户:</div><p style='text-indent:2em;text-align: left;'>您申请的业务已受理,请通过网银向下面的账户转账</p><div style='text-align: left;'><span style='width: 30%;display: inline-block;'>转入银行:</span>"
                        +data.data.recBankBranchName+"</div><div style='text-align: left;'><span style='width: 30%;display: inline-block;'>转入户名:</span>"
                        +data.data.recBankAcctName+"</div><div  style='text-align: left;'><span  style='width: 30%;display: inline-block;'>转入账号:</span>"
                        +data.data.recBankAcct+"</div><div  style='text-align: left;'><span  style='width: 30%;display: inline-block;'>转入金额:</span>"
                        +self.bizAmt+"元</div><div style='text-indent:2em;text-align: left;'>我们将在24小时内确认到账</div><div style='text-align:right'>宝三成</div><div style='text-align:right'>"+date+"</div>").then(action => {
            router.push("/personal")
          })
          // MessageBox.alert("尊敬的客户,您申请的业务已受理,请通过网银向"+data.data.recBankBranchName+"<br>户名:"+data.data.recBankAcctName+"<br>银行账号:"+data.data.recBankAcct+"<br>转入人民币:"+self.bizAmt+"元<br>我们将在24小时内确认到账").then(action => {
          //   router.push("/personal")
          // })
        }
      })
    },
    choseCard:function(){
      this.popupVis = true
    },
    back:function(){
      this.popupVis = false
    },
    chose:function(title,acct,code){
      this.popupVis = false
      this.bankTitle = title
      this.bankAcct = acct
      this.bankCode = code
    }
  },
  components:{
     custHeader
  }
}
</script>

<style lang="css" scoped>
.chargeBody{
  background-color: #eee;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 4;
}

.card{
  height: 100px;
  width: 95%;
  margin: 10px;
  background-color: #fff;
  border-radius: 7px;
}
.cards{
  height: 114px;
  text-align: center;
  line-height: 114px;
  background-color: #fff;
  border-radius: 8px;
  margin: 10px;
  font-size: 22px;
}

.choseCardBody{
  background-color: #eee;
  height: 100vh;
}






</style>



